import { useRef } from 'react'
import ReactPlayer from 'react-player/lazy'
import Carousel from 'better-react-carousel'

function GameVideos({ videos }) {
  const videoRefs = useRef([])

  const handleReady = (index) => () => {
    const videoElement = videoRefs.current[index]
    if (videoElement) {
      videoElement.classList.remove('loading')
    }
  }

  return (
    <div className=" w-full flex justify-center items-center">
      <div className="flex-1 flex justify-center items-center ">
        <ReactPlayer
          playing={true}
          muted={true}
          className="loading"
          url={`https://www.youtube.com/watch?v=${videos[0].video_id}`}
          onReady={handleReady(0)}
        />
      </div>
      {videos.length > 1 && (
        <Carousel cols={1} rows={3} gap={10} loop>
          {videos.map((video, index) => {
            return (
              <Carousel.Item key={index}>
                <ReactPlayer
                  className="loading"
                  width={'100%'}
                  height={'auto'}
                  url={`https://www.youtube.com/watch?v=${video.video_id}`}
                  onReady={handleReady(index)}
                  ref={(player) => (videoRefs.current[index] = player?.wrapper)}
                />
              </Carousel.Item>
            )
          })}
        </Carousel>
      )}
    </div>
  )
}

export default GameVideos
